// Multiple Choice Input Styling
sp-radio-group {
  .btn-outline-primary {
    color: rgba($spblue, 0.8);
    border-color: rgba($spdarkgray, 0.3);

    &:hover,
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active {
      background-color: $spblue;
      border-color: rgba($spdarkgray, 0.3);
    }
  }

  // This should not longer be needed if we switch all selects to include sp-radio-group elements
  label.btn {
    border-radius: 0.125rem;
  }

  .btn-group {
    @media screen and (max-width: 480px) {
      @include flex;
      @include flex-direction(column);
    }
  }

  sp-radio-button {
    &:not(:last-child) {
      label.btn {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }

    &:not(:first-child) {
      label.btn {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
  }

  label.btn {
    border-radius: 0.125rem;
    height: auto;

    input[type='radio'] {
      display: none;
    }

    // Checkmark Icon of Selected Item
    &::before {
      content: 'done';
      font-family: 'Material Icons';
      position: initial; // initial instead of absolute: needed to fix style in sp-radio-group
      top: 5px;
      left: 10px;
      width: 30px;
    }

    &:not(.active)::before {
      visibility: hidden;
    }

    .multi-choice-option-text {
      padding: 0 4px;
      width: 100%;
      white-space: pre-wrap;
      line-height: 1;
    }
  }

  &.select-indicator-off {
    sp-radio-button {
      label.btn {
        margin: 0;
        min-width: 0px;  // unset not works for IE11
        &::before {
          display: none;
        }

        .multi-choice-option-text {
          padding: 0;
        }
      }
    }
  }
}
