header {
  margin-bottom: 1rem;

  &.header-bold {
    background-color: $spblue;

    * {
      color: $white !important;
    }

    h1 {
      font-weight: 600 !important;
      margin-bottom: 1rem;
    }

    p {
      font-weight: 500;
    }

    .header-subtext {
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 700;
      opacity: 0.8;
    }

    a:hover {
      opacity: 0.8;
    }
  }
}
