.page-explainer-intro {

  .card-body {
    padding: 0;

    table {
      th, td {
        padding: 1.5rem;

        &:not(:last-child) {
          border-right: 1px solid rgba(0, 0, 0, 0.125);
        }
      }

      th {
        width: 30%;
      }

      td {
        background-color: $spoffwhite;
      }
    }
  }
}
