sp-review-container {
  table {
    th {
      width: 30%;
    }

    tr td:last-child {
      font-size: 80%;
      text-transform: uppercase;
      width: 10%;
    }

    tr table.table-inner {
      margin: 0 0 1.0rem 0;
    }
  }
  table.table-inner {
    td,
    th {
      border: none;
      padding: 0 0.5rem 0 0;
      margin-bottom: 0;
      width: inherit;
    }
    // Undo tr td:last-child above
    tr td:last-child {
      font-size: 100%;
      padding-right: 0;
      text-transform: none;
      width: inherit;
    }

    td {
      width: 225px;
    }
  }
}

sp-account-details-container {
  table.table-sm {
    .col1,
    .col2 {
      display: inline-block !important;
      width: 285px;
    }
  }
}
