.dashboard {
  padding-bottom: 2rem;

  header {
    background-color: $spblue;
    padding: 0.5rem 0;

    * {
      color: $white !important;
    }
  }

  .dashboard-bar {
    background-color: $white;
    border-radius: 0.125rem;

    &:not(:nth-child(5))::after {
      background-color: rgba($splightgray, 0.5);
      content: '';
      display: inline-block;
      height: 1px;
      position: relative;
      vertical-align: middle;
      width: 100%;
    }

    .dashboard-status-indicator {
      height: 48px;
      width: 48px;
      border-radius: 100%;
      padding: 0.5rem;
      margin-left: 0.5rem;
      @include flex;
      @include align-items(center);
      @include justify-content(center);

      .icon {
        font-size: 32px;
        color: $white;
      }
    }

    .dashboard-subheader {
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      color: $spmediumgray;
    }

    // Bar Hover Effects
    a {
      > div:first-child {
        padding: 0.5rem;

        &:hover {
          background-color: rgba($splightgray, 0.2);
          border-radius: 0.125rem;
          padding: 0.5rem;

          button {
            transform: translateY(-0.25rem);
            box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
          }
        }
      }
    }

    img {
      margin: 1rem;

      @media screen and (max-width: 768px) {
        width: 40px;
        height: auto;
        margin: 0 1rem 0 0.5rem;
      }
    }

    p {
      margin-bottom: 0;
    }

    h2 {
      margin-bottom: 0.5rem;
      color: $spblue !important;

      @media screen and (max-width: 768px) {
        font-size: 1rem !important;
      }
    }

    // Dashboard Bar Status Styles
    &.status-inprogress {
      .dashboard-status-indicator {
        background-color: rgba($spyellow, 0.7);
      }

      a {
        color: $spblue;

        &:hover {
          color: $splinkblue-hover;
        }
      }
    }

    &.status-complete {
      .dashboard-status-indicator {
        background-color: $spgreen;
      }

      a {
        color: $spmediumgray;

        &:hover {
          color: darken($spmediumgray, 20%);
        }
      }
    }

    &.status-action {
      .dashboard-status-indicator {
        background-color: rgba($splightgray, 0.3);

        .icon {
          color: rgba($white, 0.5);
        }
      }

      a {
        color: $spgreen;

        &:hover {
          color: darken($spgreen, 20%);
        }
      }
    }
  }

  .dashboard-action-buttons {
    margin-top: 1rem;

    button.btn {
      @media screen and (max-width: 480px) {
        width: 100%;
      }

      &:not(:first-child) {
        margin-left: 1rem;

        @media screen and (max-width: 480px) {
          margin-left: 0;
        }
      }

      &:not(:last-child) {
        @media screen and (max-width: 480px) {
          margin-bottom: 1rem;
        }
      }
    }

    @media screen and (max-width: 480px) {
      @include flex-direction(column);
    }
  }
}
