div.layout {
  background-color: $white;
}

.bg-white {
  background-color: $white;
}

.layout {
  min-height: 100vh;
  margin: 0px;
  @include flex;
  @include flex-direction(column);
}

.standard {
  width: 60rem;
  margin: auto;

  @media screen and (max-width: 992px) {
    padding: 1rem;
    max-width: 100%;
  }
}

.layout-split-container {
  @include flex;
  @include flex-direction(row);

  @media screen and (max-width: 992px) {
    @include flex-direction(column);
  }

  .layout-split-content {
    flex-grow: 2;
    padding: 0 1.5rem;

    @media screen and (max-width: 992px) {
      padding: 0;
    }

  }

  .layout-split-menu {
    min-width: 250px;
  }
}


.static-nav {
  margin-bottom: 5rem;
}

@media screen and (max-width: 769px) {
  width: auto;
}

.content {
  flex: 1;
}

// For Employee Component
.container {
  display: block;
  position: relative;
}

.section {
  margin-bottom: 1rem;
}

.clickable {
  cursor: pointer;
}

/* fix google places autocomplete */
.pac-container {
  z-index: 100000;
}
/* make google places dropdown as wide as the content */
.pac-container, .pac-item {
    width: inherit !important;
}

section {
  margin-bottom: 3rem;

  sp-signup & {
    margin-bottom: 0;
  }
}

// TODO: Something similar and consistent for error displays
.error,
.errors {
  color: $sperrorred;
  background-color: $spoffwhite;
  border-color: lighten($splightgray, 10%);
  margin: 0.5rem 0;
  padding: 2em 1em;
}

.mobile-hide {
  @media screen and (max-width: 768px) {
    display: none;
    visibility: hidden;
  }
}

.desktop-hide {
  @media screen and (min-width: 992px) {
    display: none;
    visibility: hidden;
  }
}

.mobile-stack {
  @media screen and (max-width: 480px) {
    @include flex;
    flex-direction: column !important;
    align-items: flex-start !important;
  }
}

.link-cursor {
  cursor: pointer;
}

hr {
  width: 100%;
  border-top: 1px solid lighten($splightgray, 10%);
}

.display-none {
  display: none;
}

.f-wrap {
  flex-wrap: wrap;
}
