.nav-button-container {
  @include flex;
  @include justify-content(flex-end);

  .static-nav & {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    background: linear-gradient(rgba($white, 0.5), $white);
    z-index: 10;
    padding: 1rem;
    border-top: 1px solid rgba($splightgray, 0.5);
  }

  .nav-buttons {
    max-width: 1200px;
    width: 100%;
    margin: auto;

    a:not(:last-child) {
      margin-right: 1rem;
    }
  }

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .nav-buttons {
         /* Fix for IE nav-bar rendering */
         max-width: 100%;
    }
 }

}
