.btn, button, .button {
  letter-spacing: 0.25px;
  height: 40px;
  font-weight: 700;
}

a.btn {
  border-radius: 0.125rem;

  &.input-group-text,
  button.btn.input-group-text {
    border-radius: 0.125rem;
    min-width: 1rem;
  }
}

button {
  display: flex !important;
  @include flex-direction(row);
  @include align-items(center);
  @include justify-content(center);

  &.btn-cta {
    background-color: $sporange !important;
    border-color: $sporange !important;
    color: $white !important;

    &:hover {
      background-color: darken($sporange, 10%) !important;
    }
  }

  &.btn-navigation {
    background-color: $spblue !important;
    border: $spblue !important;
    color: $white;

    &:hover {
      background-color: darken($spblue, 10%) !important;
    }
  }

  &.btn-large {
    width: 300px;
    height: 3rem;

    @media screen and (max-width: 767px) {
      width: 100%;
    }
  }

  &.btn-fix {
    position: relative;
    top: -7px;

    @media screen and (max-width: 480px) {
      width: 100%;
      margin-right: 0;
    }

  }
}

/* Don't mess with the ngb datepicker buttons! */
button.btn:not(.ngb-dp-arrow-btn) {
  border-radius: 0.125rem;
  font-size: 0.875rem !important;
}

.btn-group {
  a.btn,
  button.btn {
    border-radius: 0.125rem;
  }

  label {
    min-width: 120px;
  }

  .btn-light {
    background-color: $white;
  }
}

.btn {
  &:focus {
    box-shadow: 0 0 0 0.2rem rgba($spgreen, 0.5);
  }

  &.btn-action {
    border-radius: 0.125rem;
    height: 2rem;
    line-height: 1;
  }

  &.btn-light {
    color: $spdarkgray;
    background-color: rgba($splightgray, 0.3);

    &:hover,
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active {
      color: $spdarkgray;
      background-color: rgba($splightgray, 0.5);
      border-color: rgba($splightgray, 0.6);
    }
  }
  &.btn-super {
    height: 3rem;
    width: 100%;
  }
}

button.btn,
a.btn {
  &.disabled, &:disabled {
    opacity: 0.4;
  }
}

.btn-group-toggle label {
  display: flex !important;
  justify-content: center;
  align-items: center;
}

// Circle Icon Button

.btn-circle {
  color: $spblue;
  border-radius: 100%;
  padding: 8px;
  margin: 0 4px;
  font-size: 20px;

  &:hover {
    background-color: rgba($splightgray, 0.3);
  }

  &.btn-filled-dark {
    color: $white;
    background-color: $spblue;

    &:hover {
      opacity: 0.9;
    }
  }

  &.btn-filled-light {
    background-color: rgba($splightgray, 0.3);

    &:hover {
      background-color: rgba($splightgray, 0.5);
    }
  }
}

// Button Icon Combo (Icon with Text)
.btn-icon-combo {

  :first-child {
    margin-right: 0.25rem;
  }
  i.icon {
    font-size: 1.25rem;
  }
}

.btn-container {
  @include flex;
  @include flex-direction(row);
  margin-top: 1rem;

   @media screen and (max-width: 767px) {
    @include flex-direction(column);
     button.btn {
      margin-top: 1rem;
    }
  }

   button.btn {
    margin-right: 1rem;
  }
}
