.stepper-container {
  width: 80%;
  margin: 1rem;
  @include flex;
  @include justify-content(center);

  .stepper {
    @include flex;
    @include flex-direction(row);
    position: relative;
    width: 100%;
    max-width: 660px;
    height: 80px;
    padding: 0;
    @include justify-content(center);

    &:before {
      content: '';
      width: calc(100% - 80px);
      position: absolute;
      height: 2px;
      background-color: $spmediumgray;
      top: 16px;
      left: 40px;
      z-index: 0;
    }

    .step {
      @include flex;
      @include align-items(center);
      @include flex-direction(column);
      width: 100px;
      font-size: 12px;
      text-align: center;
      text-transform: uppercase;
      z-index: 1;
      cursor: pointer;
      position: relative;

      &:not(:last-child) {
        margin-right: 0.5rem;
      }

      &:focus {
        outline: none;

        .step-counter {
          box-shadow: 0 0 16px $spgreen;
        }
      }

      &:first-child {
        &:after {
          content: none;
        }
      }

      &.status-complete {
        .step-counter {
          background-color: lighten($spblue, 30%);
        }

        .step-text {
          color: lighten($spblue, 50%);
        }
      }

      &.status-inprogress, &.status-action {
        .step-counter {
          background-color: $white;
        }
      }

      .step-counter {
        height: 32px;
        width: 32px;
        @include flex;
        @include justify-content(center);
        @include align-items(center);
        border-radius: 50%;
        margin-bottom: 0.25rem;
      }

      .step-text {
        color: $white;
        font-size: 10px;
        font-weight: 500;
      }

      &.active {
        color: $spgreen;
        font-weight: bold;

        span:first-child {
          background-color: $spgreen;
          border-color: $spgreen;
          color: $white;
        }

        .step-text {
          color: $splightgreen;
          font-weight: bold;
        }

      }
      &.active.read-only {
        color: black;
        font-weight: 500;
        span:first-child {
          background-color: black;
          border-color: black;
        }
      }
    }
  }

  // See https://github.com/IdeaBlade/sp-x/pull/168/files#diff-5d3daf3d460d2c70304cdeb6dc8e9f2d
  // for previous CSS that we might want to revive or inspect
}

