.input-group.sp-date-picker {
  width: auto;
}

.dp-btn {
  background-color: transparent;
  margin-left: 4px;
  :active {
    border: none;
  }
  :hover {
    background-color: gainsboro;
  }
}

sp-date[picker="true"] div div input[type="text"].date-field {
  background: #fff;
  width: 9em !important;
}
.flatpickr-months .flatpickr-month {
  height: 38px;
}
sp-date div div input[type="text"].picker {
  width: 0px;
  padding: 0px !important;
  margin: 0px;
  border: none;
}
.input-group-append span.input-group-text {
  padding-top: 4px;
  padding-bottom: 4px;
}
input[type='text'].picker.flatpickr-input:focus {
  border: none;
}
