table tr:first-child {
  td,
  th {
    border: none;
  }
}

table.interactive-table {
  &.no-header {
    tr {
      cursor: pointer;

      &:hover {
        background-color: $spgreenwash;
      }
    }
  }

  a,
  a[routerLink],
  a:not([href]):not([tabindex]).link,
  a:not([href]):not([tabindex]) {
    color: unset;
    font-weight: unset;
  }

  tr:not(:first-child) {
    cursor: pointer;

    &:hover {
      background-color: $spgreenwash;
    }
  }

  th {
    background-color: $spgreenwash;
  }

  a,
  a:hover {
    color: $spdarkgray;
    text-decoration: none;
  }
}

td.multi-row {
  span {
    display: block;
  }
}

table.table-list {
  td,
  th {
    padding: 0;
    border: 0;
  }
}

table.table-collapse {
  td:first-child {
    padding-left: 0;
  }
}

.card .card-body {
  table:last-child {
    margin-bottom: 0;
  }
}

.table-subheader th {
  color: $spgreen;
  font-size: 1.125rem;
}

sp-account-details-container table th {
  width: 300px;
}

table.table-condensed {
  .table-headers th {
    text-transform: uppercase;
    padding-bottom: 0;
  }

  tr:not(:first-child) td {
    padding-top: 14px;
  }

  // Make account type select input width adjust to fit all options
  tbody tr td:nth-child(2) {
    .form-group .input-group > .form-control {
      width: auto;
    }
  }

  // Custom styles for inputs inside of table
  .btn-group label {
    min-width: 0;
  }

  sp-control {
    label {
      margin: 0;
    }

    sp-radio-group label.btn {
      margin: 0;
      height: auto;

      &.active::before {
        content: '';
        display: none;
      }
    }
  }

  .form-group {
    margin-bottom: 0;
  }
}
