sp-admin-account-list {
  @include flex;
  @include flex-direction(column);

  sp-admin-account-list-detail {
    .table {
      margin-left: 3.8rem;
      tr {
        font-size: 0.9rem;
      }

      td {
        padding: 0.4rem;
      }
    }
  }

  .datatable-row-group {
    cursor: pointer;
  }

  .datatable-body-row.active {
    a,
    a[routerLink],
    a:not([href]):not([tabindex]).link,
    a:not([href]):not([tabindex]) {
      color: white;
      &:hover {
        color: yellow;
      }
    }
  }
  label.checked {
    color: $spinfobluesecondary;
  }

    // If we add date filter
    // sp-date {
    //   height:20px;
    //   margin-top:-15px;

    //   .form-group > .input-group > input.date-field {
    //     width: 110px;
    //   }
    // }

  .expando {
    &:focus {
      outline: none;
    }
  }

  .form-control {
    border: none;
    margin-bottom: 1rem;
    padding: 0.8rem 0;
  }

  .form-check {
    padding: 0.5rem 0;

    @media screen and (max-width: 768px) {
      @include flex-wrap(wrap);
    }

    .form-check-label {
      margin-right: 1.5rem;
      font-weight: normal;
    }
  }

  // Admin Account Data Table
  ngx-datatable.ngx-datatable.bootstrap {
    font-size: 0.9rem;

    datatable-header {
      font-weight: bold;
    }

    datatable-body {
      datatable-body-row {
        cursor: pointer;

        datatable-body-cell:first-child {
          @include flex;
          @include align-items(center);
        }

        datatable-body-cell:nth-child(3) {
          font-weight: bold;
        }
      }
    }

    datatable-footer.datatable-footer {
      background: $spdarkgray;
      color: $white;
    }
  }
}
