sp-chat {
  position: fixed;
  bottom: 100px;
  right: 1rem;
  cursor: pointer;

  .chat-element {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: $spgreen;
    @include flex;
    @include align-items(center);
    @include justify-content(center);
    transition: 1s ease-out;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.2);

    &:hover {
      transform: translateY(-0.25rem);
      background-color: darken($spgreen, 5%);
    }

    i.material-icons {
      color: $white;
    }
  }
}
