input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
textarea,
select,
select:focus, {
  font-size: 16px;
  // IMPORTANT: Font size also prevents zoom on form inputs on iOS mobile
  color: rgba($spblue, 0.8);
}

input,
select,
.form-group input,
.form-group select,
.form-group .input-group > .form-control {
  width: 300px;
  flex: none;

  @media screen and (max-width: 768px) {
    width: 100%;
    max-width: 100%;
  }

  &[type='text'] {
    color: rgba($spblue, 0.8);
    border-color: rgba($spdarkgray, 0.3);
    border-radius: 0.125rem;
    border-width: 1px;
    border-style: solid;
    padding: 0.375rem 0.75rem;

    &:focus {
      border-color: $spgreen;
      border-width: 1px;
      box-shadow: none;
      border-style: solid;
      outline: none;
    }
  }
}

sp-input,
sp-select {
  .form-group {
    margin-bottom: 1.5rem;
  }

  label {
    margin-bottom: 0.5rem;
  }

  input,
  select,
  .form-control {
    color: rgba($spblue, 0.8);

    &.is-valid {
      border-color: rgba($spdarkgray, 0.3);

      &:focus {
        border-color: $spgreen;
        border-width: 1px;
        box-shadow: none;
      }
    }

    &.is-invalid:focus {
      box-shadow: none;
      border-width: 1px;
    }

    &::placeholder,
    &::-webkit-input-placeholder {
      color: rgba($spdarkgray, 0.4);
      font-style: italic;
    }
  }
}

.input-medium .form-group .input-group {
  > .form-control {
    max-width: 180px;

    @media screen and (max-width: 768px) {
      width: 100%;
      max-width: 180px;
    }
  }
}

.input-medium,
.input-small {
  .form-group .input-group {
    &.input-prepend,
    &.input-append {
      .form-control {
        @media screen and (max-width: 768px) {
          flex: 1 1 auto;
          width: 1%;
        }
      }
    }
  }
}

sp-address-form {
  input,
  select,
  .form-group input,
  .form-group select,
  .form-group .input-group > .form-control {
    max-width: 300px;
    width: auto;
    flex: 1 1 auto;

    @media screen and (max-width: 768px) {
      width: 100%;
      max-width: 100%;
    }
  }
}

input[type='checkbox'] {
  width: 1.5rem;
  height: 1.5rem;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.input-split,
.input-split .input-group {
  sp-control,
  sp-input,
  sp-select {
    @media screen and (min-width: 769px) {
      margin-right: 1rem;
    }
  }

  @media screen and (max-width: 769px) {
    @include flex;
    @include flex-direction(column);
  }
}

sp-input .input-group > .form-control,
.input-group > .custom-select {
  @media screen and (max-width: 769px) {
    width: 100%;
  }

  @media screen and (max-width: 480px) {
    max-width: 100%;
  }
}

sp-input {
  &.input-xsmall .form-group .input-group > .form-control {
    min-width: 48px;
    width: 48px;

    @media screen and (max-width: 769px) {
      width: 100%;
    }

    @media screen and (max-width: 480px) {
      max-width: 100%;
    }
  }

  &.input-small .form-group .input-group > .form-control {
    min-width: 80px;
    width: 80px;

    @media screen and (max-width: 769px) {
      width: 100%;
    }

    @media screen and (max-width: 480px) {
      max-width: 100%;
    }
  }

  &.input-medium .form-group .input-group > .form-control {
    min-width: 180px;
    width: 180px;

    @media screen and (max-width: 769px) {
      width: 100%;
    }

    @media screen and (max-width: 480px) {
      max-width: 100%;
    }
  }

  .input-group {
    max-width: 300px;

    @media screen and (max-width: 480px) {
      max-width: 100%;
    }

    .form-control:not(:last-child) {
      border-radius: 0.125rem;
    }

    &.input-prepend {
      input.form-control {
        border-radius: 0 2px 2px 0;

        @media screen and (max-width: 480px) {
          max-width: 90%;
        }
      }
    }

    &.input-append {
      input.form-control {
        border-radius: 0.125rem 0 0 0.125rem;

        @media screen and (max-width: 480px) {
          max-width: 90%;
        }
      }

      .input-group-append span.input-group-text {
        border-radius: 0 0.125rem 0.125rem 0;
      }
    }
  }
}

sp-input,
sp-control,
sp-select {
  .input-group-text,
  select,
  .form-control {
    border-radius: 0.125rem;
  }
}

.input-button-combo button {
  margin: auto 0;
}

sp-input + .form-check {
  label {
    margin-left: 0.25rem;
  }
}

sp-address-form sp-input .input-group {
  @include flex-direction(column);
}

.input-action-icon,
a.input-action-icon,
a[routerLink].input-action-icon,
a.input-action-icon:not([href]):not([tabindex]).link,
a.input-action-icon:not([href]):not([tabindex]) {
  display: flex;
  float: right;
  margin-left: -35px;
  margin-top: 5px;
  position: relative;
  z-index: 2;
  color: rgba($spblue, 0.8) !important;
}
// Because LastPass/Chrome puts own icons in a password box
.input-action-icon.nudge-left {
  margin-left: -46px;
}

.input-help-text,
.input-help-text > * {
  font-size: 0.8rem;
}

.feedback {
  display: block;
}

.form-check {
  @media screen and (max-width: 768px) {
    padding: 0;
    margin-top: 0.75rem;
  }
}

// styles for multimask and maskview, used for tax screens
input[spmultimask],
input.maskview {
  font-family: monospace, monospace;
  font-size: 12pt;
}

input[spmultimask],
input[spmultimask].form-control:focus {
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 2;
}

input.maskview,
.input-group input.maskview {
  position: absolute;
}
