/* OVERRIDE DAY OF WEEK STYLES TO ONLY SHOW FIRST CHARACTER */
sp-date-range-pay-period .ngb-dp-weekday {
  text-transform: uppercase;
  font-size: 11px;
  color: #fff !important; 
  font-style: normal !important;
  padding-left: 7px;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -o-user-select: none;
  -moz-user-select: none;
}

sp-date-range-pay-period .ngb-dp-month-name {
  background-color: #fff !important;
}

sp-date-range-pay-period .ngb-dp-week .ngb-dp-weekday:nth-child(6):first-letter,
sp-date-range-pay-period .ngb-dp-week .ngb-dp-weekday:nth-child(7):first-letter {
  color: #c6c6c6 !important;
}

sp-date-range-pay-period .ngb-dp-weekday:first-letter {
  color: #444; 
}

sp-date-range-pay-period ngb-datepicker .ngb-dp-day.disabled {
  color: #c6c6c6;
}

sp-date-range-pay-period .ngb-dp-day:focus {
  outline: 0 !important;
}

sp-date-range-pay-period .ngb-dp-navigation-chevron {
  border-color: #999 !important;
  border-width: .1em .1em 0 0 !important;
}

sp-date-range-pay-period .ngb-dp-header, sp-date-range-pay-period .ngb-dp-week {
  background-color: #fff !important;
}

sp-date-range-pay-period .pay-period-label {
  font-size: 13px;
  line-height: 13px;
  font-weight: 500;
  color: #879a8c;
  margin: 3px 0 3px 0;
  text-transform: uppercase;
}

sp-date-range-pay-period .pay-period-date {
  font-size: 19px;
  color: #444;
}

sp-date-range-pay-period .custom-day {
  text-align: center;
  padding: 0.185rem 0.25rem;
  display: inline-block;
  height: 2rem;
  width: 2rem;
}

sp-date-range-pay-period .custom-day.focused {
  background-color: #fff;
}

sp-date-range-pay-period .custom-day.range, sp-date-range-pay-period .custom-day.period-date:hover {
  background-color: #DBE5C3;
  color: white;
}

sp-date-range-pay-period div:not(.disabled) > .check-day-hover:hover, sp-date-range-pay-period .check-day {
  opacity: 1 !important;
  background-color: #FF7A00 !important;
  border: 1px solid #FF7A00 !important;
  color: #fff;
}

sp-date-range-pay-period .custom-day.range:not(.faded) {
  background-color: #B1C389 !important;
}

sp-date-range-pay-period .custom-day.faded {
  background-color: #DBE5C3;
}

sp-date-range-pay-period .period-date:hover {
  background-color: #B1C389 !important;
}

sp-date-range-pay-period .cursor-disabled {
  cursor: not-allowed !important;
}

/* Status/Nav Section */
sp-date-range-pay-period .pay-period-labels {
  width: 100%;
}

sp-date-range-pay-period .pay-period-section {
  padding: 15px;
  background-color: #f3f3f3;
  cursor: pointer;
  height: 75px;
}

sp-date-range-pay-period .ngb-dp-arrow-btn {
  height: auto;
}

/* Border between pay period blocks */
sp-date-range-pay-period .pay-period-section:nth-child(2) {
  border-top: 1px solid #DEE0E3;
  border-bottom: 1px solid #DEE0E3;
}

sp-date-range-pay-period .pay-period-labels>div>h6:first-child {
  margin: 0;
}

sp-date-range-pay-period .no-gutters {
  margin-right: 0;
  margin-left: 0;sp-date-range-pay-period 

  > .col,
  > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

/* Semi-Monthly Form Controls */
sp-date-range-pay-period .frequency-semi-monthly .date-field {
  width: 9em !important;
}

sp-date-range-pay-period .frequency-semi-monthly .input-group-append {
  margin-left: -3px;
}

/* Responsive styles */
@media screen and (max-width: 730px) {
  // Repositioning tooltips to prevent them extending offscreen on mobile devices
  sp-date-range-pay-period #tooltipZone {
    position: absolute;
    left: 0;
    margin-left: -125px;
    top: 30px;
  }

  .frequency-semi-monthly #tooltipZone {
    margin-left: -300px;
  }
}

@media screen and (max-width: 700px) {
  sp-date-range-pay-period .pay-period-container > .row {
    margin-right: 0;
    margin-left: 0;
    > .col,
    > [class*="col-"] {
      padding-right: 0;
      padding-left: 0;
    }
  }
}

@media screen and (max-width: 615px) {
  sp-date-range-pay-period .pay-period-labels {
    padding-left: 0;
  }

  sp-date-range-pay-period .frequency-semi-monthly .date-field {
    max-width: 8em !important;
  }
}

@media screen and (max-width: 576px) {

  sp-date-range-pay-period .frequency-semi-monthly .date-field {
    width: 9em !important;
  }

  sp-date-range-pay-period .pay-period-section {
    border: 1px solid #DEE0E3;
  }

  sp-date-range-pay-period .pay-period-section:nth-child(2) {
    border-left: none;
    border-right: none;
  }

  sp-date-range-pay-period .pay-period-labels {
    padding-right: 0;
    margin-bottom: 20px;
  }

  sp-date-range-pay-period .pay-period-label {
    font-size: 12px; 
    font-weight: 700;
  }

}

@media screen and (max-width: 490px) {

  sp-date-range-pay-period .pay-period-labels > row {
    margin-right: -1em;
  }

  sp-date-range-pay-period .pay-period-label {
    font-size: 10px; 
  }

  sp-date-range-pay-period .pay-period-container {
    padding-left: 0;
    padding-right: 0;
  }

  sp-date-range-pay-period .ngb-dp-month:first-child .ngb-dp-week {
    padding-left: 0 !important;
  }

  sp-date-range-pay-period .pay-period-date {
    font-size: 17px;
  }

  sp-date-range-pay-period sp-radio-group {
    width: 100%;
  }

}

@media screen and (max-width: 457px) {
  sp-date-range-pay-period .ngb-dp-months {
    margin-left: -10px;
  }
}

@media screen and (max-width: 457px) {
  sp-date-range-pay-period .pay-period-labels > row {
    margin-right: -40px;
  }

  sp-date-range-pay-period .frequency-semi-monthly .ngb-dp-months {
    margin-left: 0px !important;
  }
}
