sp-ee-payment-accounts {

  .btn-clear {
    position: relative;
    top: 6px;
  }
  .input-xsmall2 {
    min-width: 30px;
    width: 30px;
  }

  // Test
  input,
  select,
  .form-group input,
  .form-group select,
  .form-group .input-group > .form-control {
      flex: 1 1 auto;
      width: 1%;
  }

  .payment-container {
    padding-bottom: 1rem;

    &:not(:last-child) {
      border-bottom: 1px solid #ddd;
    }

    .payment-description {
      @include flex;
      @include flex-direction(row);
      @include align-items(center);
      padding: 0.5rem 0;
      margin-bottom: 1rem;
    }

    .payment-details {
      @include flex;
      @include flex-direction(row);
      @include align-items(center);

      > *:not(:last-child) {
        margin-right: 1rem;
      }

      @media screen and (max-width: 768px) {
        @include flex-direction(column);
        @include align-items(flex-start);
      }
    }

    input,
    select,
    .form-group input,
    .form-group select,
    .form-group .input-group > .form-control {
      &:not(.input-small) {
        flex: 1 1 auto;
      }
    }

    .input-group {
      flex-wrap: nowrap;
    }
  }

  .liquid-design {
    display: flex;
    flex-direction: column;
  }
  .wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    vertical-align: bottom;
    border-top: 1px solid #eee;
  }
  .title {
    min-height: 20px;
  }
  .item-counter {
    flex-grow: 1;
    flex-shrink: .1;
    flex-basis: 1em;
    padding: 29px 1px 1px 0px;
  }
  .item-type-account {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 147px;
    padding: 1px 1px;
  }
  .item-routing {
    flex-grow: 1;
    flex-shrink: .5;
    flex-basis: 120px;
    padding: 1px 1px;
  }
  .item-account-number {
    flex-grow: 1;
    flex-shrink: .5;
    flex-basis: 120px;
    padding: 1px 1px;
  }
  .item-allocation {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 70px;
    padding: 1px 1px;
  }
  .item-amount {
    flex-grow: 0;
    flex-shrink: .1;
    flex-basis: 150px;
    padding: 1px 1px;
  }
  .item-clear {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 80px;
    padding: 32px 0px 18px 1px;
  }
}
