.layout-split-content:last-child {
  border-left: 1px solid lighten($splightgray, 10%);

  @media screen and (max-width: 992px) {
    border: none;
  }
}

sp-company-deductions {
  .sp-card {
    width: 70%;
    min-width: 30rem;
    padding: 0.75em 1rem;

    @media screen and (max-width: 992px) {
      width: 100%;
      min-width: auto;
    }

    &.sp-card-helper {
      @include flex;
      @include flex-direction(column);
      align-items: center;
    }
  }

  sp-company-deductions-table {
    h3 {
      margin: 1.5rem 0;
    }
  }

  sp-deductions-catalog {
    > div:first-child {
      padding-right: 0.7rem;
    }

    i.icon {
      font-size: 95%;
      width: 1rem;
    }

    span.editing {
      font-weight: bold;
    }
  }
}

// Side Selector Menu (Accordions)

sp-deductions-catalog {
  i.icon {
    font-size: inherit;
  }
}

// Tax Item Form

.tax-item-form {

  @media screen and (max-width: 768px) {
    border-top: 1px solid lighten($splightgray, 10%);
    padding: 1rem 0;
  }

  .tax-item-header {
    @include flex;
    @include justify-content(between);
    @include align-items(center);

    a i.icon {
      width: 36px;
      background-color: $spoffwhite;

      &:hover {
        background-color: darken($spoffwhite, 5%);
      }
    }
  }

  .tax-item-description {
    margin-bottom: 1.5rem;
  }
}

// TODO - may need to be moved to a more generic place
.percent-inline {
  display: inline-block;
  margin: 0em 1em;

  .form-group .input-group > .form-control {
    width: 47px;
  }
}

// TODO - may need to be moved to a more generic place
.aop-inline {
  display: inline-block;
  margin: 0em 1em;
}

// TODO - use existing generic inline input classes
.input-abbreviation div div input[type='text'].form-control {
  min-width: 120px; // space for 7 chars
  width: 120px;
}

.employee-deduction-catalog {
  list-style: none; // TODO: review and refactor this: Stefanie
}

.default-earnings-container {
  list-style: none;
  padding-left: 0;
  margin-top: 20px;
  margin-bottom: 30px;
  display: block;
  overflow: hidden;
}

