.navbar {
  background-color: $spblue;

  &.read-only {
    background-color: $sperrorred;
  }

  > * {
    width: 100%;
    max-width: 1200px;
    margin: auto;
  }

  .navbar-content {
    @include flex;
    @include justify-content(space-between);

    .navbar-brand {
      padding: 0.5rem;

      img#logo-link {
        height: 32px;
        cursor: pointer;

        @media screen and (max-width: 768px) {
          height: 20px;
        }
      }
    }

    .navbar-nav {
      @media screen and (max-width: 768px) {
        @include flex-direction(row);
      }
    }

    .nav-link {
      font-weight: bold;
      color: $white !important;
      font-size: 12px;
      line-height: 1.75;

      &.active a {
        color: $white;
      }

      &:hover:not(.btn) {
        opacity: 0.8;
      }
    }

    .nav-user-details {
      @media screen and (min-width: 576px) {
        margin-left: auto;
      }
    }

    .navbar-text {
      color: $white;
      font-size: 12px;
      padding: 0.5rem 1rem;
    }

    .sp-user-name {
      @media screen and (max-width: 769px) {
        display: none !important;
      }
    }
  }
}
