.fein-check {
  margin-top: 35px;
  margin-left: 10px;
}

.tin-check-block {
  margin-bottom: 1rem;
}

.verifying,
.blocked,
.verified,
.legalname-missing
.service-unavailable {
  margin-top: 2em;
  text-align: left;
  height: 8em;
}

.btn-update {
  margin-top: 28px;
  margin-left: 22px;

  @media screen and (max-width: 768px) {
    width: 100%;
    margin: 1rem 0;
  }
}

.link-edit-data {
  position: relative;
  top: 31px;

  @media screen and (max-width: 480px) {
    position: static;
    margin-top: 1rem;
  }
}

#show-tin-response {
  margin-top: 0.7rem;
  font-size: 90%;
}

.tin-check.info-pill {
  padding-left: 0 !important;
}

.tin-check.info-pill + button {
  @media screen and (max-width: 480px) {
    margin-top: 1rem;
    width: 100%;
  }
}

.fixed-width-300 {
  width: 300px;
}
.position-verify {
  position: relative;
  left: -80px;
}
@media screen and (max-width: 480px) {
  .position-verify {
    position: relative;
    left: -8px;
  }
}

.tin-check.modal-body p,
sp-tincheck-review-advice p {
  margin: 1rem 0 !important;
}
