.accordion {
  @include flex;
  @include flex-direction(column);
  display: flex;
  flex-flow: column;
  width: 100%;
  margin-bottom: 0.5rem;

  .accordion-group-header-container {
    @include flex;
    @include flex-direction(row);
    @include align-items(center);
    cursor: pointer;

    .accordion-group-header {
      font-size: 1rem;
      font-weight: bold;
      color: $spblue;
    }
  }

  .content {
    flex-grow: 1;
  }

  .toggle-base {
    @include animation-rotate(0deg);
  }

  .collapsed {
    @include animation-rotate(-90deg);
  }

  ul {
    list-style: none;
    padding: 0px;
    margin: 0;

    li.accordion-menu-item {
      margin-bottom: 0.5rem;
      cursor: pointer;

      &:first-child {
        margin-top: 0.5rem;
      }

      span {
        font-weight: bold;
        font-size: 1rem;
        color: $splinkblue;

        &.item-unavailable {
          color: $spmediumgray;
          cursor: auto;
          margin-left: 19px;

          &:hover {
            color: $spmediumgray;
          }
        }

        &:hover, &.item-active {
          color: darken($splinkblue, 20%);
        }
      }
    }
  }
}
