.badge.badge-sp {
  font-weight: normal;
  line-height: 1.5;
  text-transform: uppercase;
  padding: 4px 8px;
  font-weight: 700;
  border-radius: 0.125rem;

  &.badge-lg {
    font-size: 14px;
    padding: 8px;
  }

  &.badge-action {
    color: $white;
    background-color: $spgreen;
  }

  // Update

  &.badge-inprogress {
    color: $white;
    background-color: rgba($sporange, 0.6);
  }

  &.badge-complete {
    color: $white;
    background-color: $spgreen;
  }

  &.badge-warning {
    color: $spdarkgray;
    background-color: $spyellow;
  }
}

