ul.nav-tabs {
  @include flex;
  @include align-items(center);
  border: none;
  margin-bottom: 2rem;

  @media screen and (max-width: 768px) {
    display: block;
    overflow: auto;
    width: 100%;
    white-space: nowrap;
  }

  .nav-item {
    background-color: $spoffwhite;
    margin-right: 0;
    border-color: $white;

        @media screen and (max-width: 768px) {
          display: inline-block;
        }

    .nav-link {
      @include flex;
      padding: 0.25rem 1rem;
      font-size: 18px;
      font-weight: 700;
      text-transform: uppercase;
      border-radius: 0;
      border-color: $white;

      &:first-child {
        border-left: 1px solid $white;
      }

      &.disabled {
        cursor: pointer;
        color: $spmediumgray;
      }

      &.active {
        color: $splinkblue;
        background-color: $white;
        font-weight: bold;
        border-bottom-color: $white;
        border-left-color: $white;
        border-right-color: $white;

        &:hover {
          background-color: $white;

          a {
            color: darken($splinkblue, 10%);
            border-top: 3px solid $splinkblue;
          }
        }

        a {
          color: $splinkblue;
          border-top: 3px solid $splinkblue;
        }
      }

      &:hover {
        border-color: $white;
        background-color: darken($spoffwhite, 5%);

        a {
          color: darken($spmediumgray, 5%);
        }
      }

      a {
        padding: 0.5rem 1rem;
        margin: -0.5rem -1rem;
        color: $spmediumgray;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.5px;
        text-transform: uppercase;


        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}
