.tooltip-icon {
  margin: 0 4px;
  color: $spinfobluesecondary;

  &:hover {
    opacity: 0.9;
  }
}

.alert-icon {
  height: 3rem;
  width: 3rem;
  font-size: 2rem;
  color: $white;
  background: $spgreen;
  border-radius: 100%;
  padding: 1rem;
  @include flex;
  @include align-items(center);
  @include justify-content(center);
}

.step-icon {
  height: 32px;
  width: 32px;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 100%;
  border: 3px solid $spblue;
  @include flex;
  @include align-items(center);
  @include justify-content(center);

  span {
    color: $spblue;
    font-size: 16px;
  }
}

* {
  &.icon-right {
    .icon {
      margin-left: 1rem;
    }
  }

  &.icon-left {
    .icon {
      margin-right: 1rem;
    }
  }
}
a > .icon {
  width: 24px;
}
