@mixin control-panel-layout {
  margin: 8px;
  margin-left: 1em;
  display: flex;
  flex-flow: column wrap;
  > * {
    flex: 1 100%;
  }
  .button-panel {
    flex: 1 auto;
    margin: 8px 0px;
    order: 1;
  }
  .filter-panel {
    flex: 1 auto;
    margin: 8px 0px;
    order: 2;
  }
}

@mixin content-container-layout {
  display: flex;
  flex-flow: row wrap;
  > * {
    flex: 1 100%;
  }
  .list-container {
    flex: 1 auto;
    order: 1;
    max-width: 30em;
  }
  .detail-container {
    flex: 1 auto;
    order: 2;
    max-width: 22em;
  }
}

@mixin error {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
  margin: 0.5rem 0;
  padding: 2em 1em;
}

// Flex Mixins - Cross Browser
@mixin flex {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

@mixin flex-direction($value: row) {
  @if $value == row-reverse {
    -webkit-box-direction: reverse;
    -webkit-box-orient: horizontal;
    -moz-box-direction: reverse;
    -moz-box-orient: horizontal;
  } @else if $value == column {
    -webkit-box-direction: normal;
    -webkit-box-orient: vertical;
    -moz-box-direction: normal;
    -moz-box-orient: vertical;
  } @else if $value == column-reverse {
    -webkit-box-direction: reverse;
    -webkit-box-orient: vertical;
    -moz-box-direction: reverse;
    -moz-box-orient: vertical;
  } @else {
    -webkit-box-direction: normal;
    -webkit-box-orient: horizontal;
    -moz-box-direction: normal;
    -moz-box-orient: horizontal;
  }
  -webkit-flex-direction: $value;
  -ms-flex-direction: $value;
  flex-direction: $value;
}

@mixin justify-content($value: flex-start) {
  @if $value == flex-start {
    -webkit-box-pack: start;
    -moz-box-pack: start;
    -ms-flex-pack: start;
  } @else if $value == flex-end {
    -webkit-box-pack: end;
    -moz-box-pack: end;
    -ms-flex-pack: end;
  } @else if $value == space-between {
    -webkit-box-pack: justify;
    -moz-box-pack: justify;
    -ms-flex-pack: justify;
  } @else if $value == space-around {
    -ms-flex-pack: distribute;
  } @else {
    -webkit-box-pack: $value;
    -moz-box-pack: $value;
    -ms-flex-pack: $value;
  }
  -webkit-justify-content: $value;
  justify-content: $value;
}

@mixin align-items($value: stretch) {
  @if $value == flex-start {
    -webkit-box-align: start;
    -moz-box-align: start;
    -ms-flex-align: start;
  } @else if $value == flex-end {
    -webkit-box-align: end;
    -moz-box-align: end;
    -ms-flex-align: end;
  } @else {
    -webkit-box-align: $value;
    -moz-box-align: $value;
    -ms-flex-align: $value;
  }
  -webkit-align-items: $value;
  align-items: $value;
}

@mixin flex-wrap($value: nowrap) {
  // No Webkit/FF Box fallback.
  -webkit-flex-wrap: $value;
  @if $value == nowrap {
    -ms-flex-wrap: none;
  } @else {
    -ms-flex-wrap: $value;
  }
  flex-wrap: $value;
}

@mixin flex-grow($int: 1) {
  -webkit-box-flex: $int;
  -moz-box-flex: $int;
  -webkit-flex-grow: $int;
  -ms-flex: $int;
  flex-grow: $int;
}

// Animations
@mixin animation-3d-hover($color) {
  &:not(:disabled) {
    transition: all 0.2s ease-in-out;

    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 4px 17px rgba($color, 0.35);
    }
  }
}

@mixin grow($scale) {
  transition: all .2s ease-in-out;

  &:hover {
    transform: scale($scale);
  }
}

@mixin animation-rotate($degrees) {
  animation: spin .5s ease-in-out 0s 1 forwards normal;
  transition-duration: 0.5s;
  transform: rotate($degrees);
}
