* {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 0.9rem;
}

body,
p,
li,
ul,
ol,
tr,
td {
  color: rgba($spblue, 0.8) !important;
  font-weight: 400;
  line-height: 1.75;
  letter-spacing: 0.25px;

  > a,
  span {
    line-height: 1.75;
    letter-spacing: 0.25px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif !important;
  font-weight: 700;
  color: $spblue;
}

p,
ol,
ul,
li,
span,
table {
  font-size: 0.9rem;
}

tr {
  font-size: 1rem;
}

h1,
.h1 {
  font-size: 40px;
  font-weight: 700 !important;
  color: $spgreen;
  margin-bottom: 1.5rem;
}

h2,
.h2 {
  font-size: 20px !important;
  font-weight: 700 !important;
}

h3 {
  font-size: 1.125rem !important;
}

a,
a[routerLink],
a:not([href]):not([tabindex]).link,
a:not([href]):not([tabindex]) {
  cursor: pointer;
  color: $splinkblue;;
  text-decoration: none !important;
  font-weight: 600;
  margin-right: unset;

  &:hover {
    text-decoration: none !important;
    color: $splinkblue-hover;
  }
}

header .nav-link {
  padding-left: 0;
}

ol,
ul {
  padding-left: 1rem;
}

strong {
  font-weight: bold;
}
