.icon-selection-option span {
  font-size: 13px;
  font-weight: 600;
}

.icon-selection-column p {
  font-size: 11px;
  line-height: 16px;
  margin: 0 auto;
  color: #999 !important;
}

.icon-selection-column > div {
  margin-bottom: 10px;
}

.icon-selection-option {
  cursor: pointer;
  display: flex;
  font-size: 11px;
  justify-content: center;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -o-user-select: none;
  -moz-user-select: none;
  transition: .3s background-color ease-in;
}

.icon-frequency {
  width: 55px;
  height: 55px;
  margin: 0 auto 5px auto;
  display: block;
}

/* Selected Icon Bars */
.icon-selection-option.selected svg .bar {
  transition: .3s all ease-in;
}

@media screen and (max-width: 576px) {
  .icon-selection-option:not(:last-child)  {
    margin-bottom: 30px;
  }
}