.tooltip-container {
  position: relative;

  .label-tooltip {
    min-width: 300px;
    position: absolute;
    z-index: 10;
    margin-bottom: 1rem;
    padding: 1rem;
    background-color: lighten($splightgray, 20%);
    border: 1px solid lighten($splightgray, 10%);
    border-radius: 0.125rem;
    font-size: 12px;
    font-weight: normal;

    > * {
      font-size: 12px;
    }

    p:last-of-type {
      margin-bottom: 0;
    }
  }
}
